      type: 'mediumTank',
      guns: [41732],
      multination: null,
      description:
        'Танк Т-29 был разработан в 1934 году в АТБ технического отдела ЭКУ ОГПУ и предназначался для замены танка Т-28. Экспериментальный образец был изготовлен в Ленинграде на опытном заводе Спецмаштреста им. С.М. Кирова (завод №185) в 1935 году. Ведущим инженером машины был Н. В. Цейц. Танк имел два типа движителя: гусеничный и колёсный. В феврале 1936 года машина была принята на вооружение и начала производиться на ЛКЗ. Было выпущено всего несколько машин, после чего производство было остановлено, так как конструкция танка оказалась сложной, а ходовая часть обладала низкой надёжностью.',
