      description:
        'Средний танк Pz.Kpfw. IV Ausf. A в период разработки считался тяжёлым танком. Является ранней версией самого массового танка Германии. Разработка была начата в 1935 году. Серийное производство началось осенью 1937 года. Pz.Kpfw. IV находился в процессе постоянной модификации. Данная машина воссоздаёт промежуточную версию танка Ausf. A-Ausf. В. Первые три машины поступили в войска в январе 1938 года. Всего было выпущено 35 машин, являвшихся модификациями Ausf. A и состоявших на вооружении до весны 1941 года.',
