      multination: null,
      description:
        'Pz.Kpfw. IV Ausf. H Ankou — флагман команды «Удильщик». Под управлением пяти студенток старшей школы Оараи во главе с Михо Нисидзуми машина вышла победителем во многих сражениях, в том числе против школ Сандерс, Анцио, Правда и Куромориминэ. Впервые танк обнаружили члены клуба сенся-до, занимавшиеся поиском подходящей техники для соревнований. Машина много лет простояла в гараже клуба и была в удручающем состоянии. Однако броня и ходовая были исправны, и команда приняла решение отремонтировать танк. После починки его модернизировали с помощью различных найденных узлов и агрегатов.',
