      description:
        'Выпуск модификации J начался с марта 1941 года. Машина помимо усиления бронирования получила новое весьма удачное 50-мм орудие KwK38, а 1067 машин были вооружены орудием с ещё большей пробиваемостью — KwK39. Попытка установить 75-мм орудие (модификация N) была признана неудачной, и самыми крупносерийными стали модификации J, L и M с хорошо зарекомендовавшими себя 50-миллиметровками. Всего была выпущена 5691 машина, из них 1969 с длинноствольной и 2391 с короткоствольной 50-мм пушкой.',
