      type: 'mediumTank',
      guns: [1540, 2820, 6916, 3076, 7172],
      multination: null,
      description:
        'КВ-13 (объект 233) — опытный советский средний танк периода Второй мировой войны. Был создан в СКБ-2 Челябинского Кировского завода в конце 1941 – начале 1942 года в качестве «универсального танка», предназначенного для замены в производстве как средних Т-34, так и тяжёлых КВ.\nПервый прототип КВ-13 был изготовлен весной 1942 года, однако проведённые осенью того же года испытания показали его низкую механическую надёжность, а также необходимость усиления бронезащиты и введения трёхместной башни. Хотя в декабре 1942 года было начато изготовление двух доработанных прототипов, в конструкции которых были учтены эти требования, работы по КВ-13 как среднему танку были прекращены в пользу продолжения выпуска Т-34. Дальнейшее развитие проекта с использованием двух доработанных прототипов привело к созданию в 1943 году серийного танка ИС-1.',
