      type: 'heavyTank',
      guns: [34308],
      multination: null,
      description:
        'Машина является модернизированным вариантом танка ИС-2. Модернизация проводилась с начала 1950-х и до начала 1960-х годов. До создания Т-10 ИС-2 оставался основным тяжёлым танком СССР и был снят с вооружения только в середине 90-х годов ХХ в. Существенным изменениям подверглись ходовая, силовая установка и башня. Ряд деталей для ходовой был позаимствован у танка ИС-3. От него же использовались грязевые щитки и типовые ящики-бункеры для ЗИП. На башне убрали кормовой пулемёт — на его месте установили дополнительный вентилятор боевого отделения. Все пистолетные амбразуры заварили, использовав штатные пробки. На модернизированных машинах заменили двигатель на В-54К-ИС. Был увеличен боекомплект до 34–35 выстрелов. Вооружение осталось прежним (122-мм пушка Д-25), но был улучшен её подъёмный механизм. На нескольких машинах установили прибор ночного видения ТВН-2. Также была заменена радиостанция на Р-113. Были улучшены смотровые приборы механика-водителя.',
