      type: 'heavyTank',
      guns: [46340],
      multination: null,
      description:
        'Объект 279 (р) — ранний вариант эскизного проекта тяжёлого танка высокой проходимости с новой компоновочной схемой. Машина разработана Л. С. Трояновым в 1947–1948 годах. На танке предусматривалось использовать низкий четырёхгусеничный движитель, смонтированный на продольных балках, которые одновременно служили ёмкостью для топлива, что сильно снижало шанс возникновения пожара. Подобные решения значительно повышали проходимость и живучесть машины: танк мог продолжать движение с повреждённой гусеницей. Расположение корпуса над ходовой позволяло расширить боевое отделение, что, в свою очередь, делало возможным увеличить боезапас и реализовать автоматизацию процесса заряжания для повышения скорострельности. В 1948 году был построен действующий макет.',
