      multination: null,
      description:
        'Самоходная установка ИСУ-152К (название прототипа — Объект 241К) являлась модернизированным вариантом тяжёлой самоходки ИСУ-152, созданной во время Великой Отечественной войны на базе танка ИС. Модернизация проводилась на ЛКЗ в 1955–1958 годах. В результате второго этапа модернизации на ИСУ-152К по сравнению с ИСУ-152М был в 1,65 раза повышен запас хода за счёт увеличения ёмкости внутренних топливных баков. Многие из применённых агрегатов и узлов были унифицированы с машинами Т-10, Т-54 и ПТ-76.',
