      description:
        'Первые прототипы Kanonenjagdpanzer были построены в 1960 году компаниями Hanomag и Henschel для ФРГ, а серийный выпуск продолжался вплоть до 1967 года. Всего для Бундесвера было построено 770 единиц, а для вооружённых сил Бельгии — 80. Начиная с 1983 года эта самоходка перестала отвечать требованиям современной войны. По утверждению производителя, их можно было перевооружить 105-мм пушками, но часть машин переоборудовали в наблюдательные, а часть — в противотанковые ракетные установки. В территориальных войсках Kanonenjagdpanzer (известные так же как Jagdpanzer Kanone 90mm) стояли на вооружении до 1990 года.',
