import { nationList, vehicleTypes } from './temp_data/generalData';
import { vehiclesData } from './temp_data/vehiclesData';
import { vehicleDataById } from './temp_data/vehicleDataById';

if (module.hot) {
  module.hot.accept();
}

window.dataStore = {
  filters: {},
  vehicle_name_compare: [],
  vehiclesData,
  vehicleDataById
};

const mainApp = document.querySelector('#app-root');

window.renderVehicleList = function () {
  let searchList = window.dataStore.vehiclesData;
  Object.entries(window.dataStore.filters).map(filter => {
    const [key, value] = filter;
    searchList = listOfFilters[key](searchList, value);
  });
  renderVehicle(searchList);
};

const listOfFilters = {
  nation: (list, nationTank) => {
    return Object.values(list).filter(tank => tank.nation === nationTank);
  },
  type: (list, typeTank) => {
    return Object.values(list).filter(tank => tank.type === typeTank);
  },
};

const showId = (aaa, vehiclesData, vehicleDataById) => {
  const idVehicle = aaa.dataset.id;
  const infoWrap = document.querySelector('.infoVehicle-wrap');
  infoWrap.innerHTML = `
    <b>${vehiclesData[idVehicle].description}</b>
    <br>
    <b>${vehicleDataById[0].weight}</b>`;
  // console.log(vehicleDataById);
  // console.log(';;d;;');
};

const renderVehicle = data => {
  const vehicleWrap = document.querySelector('.vehicle-wrap');
  const list = Object.values(data)
    .map(vehicle => {
      return `
        <div 
          class="vehicle-item" 
          data-id="${vehicle.tank_id}" 
          onclick="(${showId})(this, window.dataStore.vehiclesData, window.dataStore.vehicleDataById)"
        > 
          <span 
            class="vehicle-type ${
              vehicle.is_premium ? `type-${vehicle.type}--premium` : `type-${vehicle.type}`
            }"
          >
            ${vehicle.tier}
          </span>
          <span 
            class="vehicle-flag vehicle-flag-${vehicle.nation}"
          ></span>
          <img 
            class="vehicle-img" 
            src="${vehicle.images.big_icon}" 
            alt="${vehicle.short_name}"
          > 
          <span 
            class="vehicle-title ${vehicle.is_premium ? 'vehicle-title--premium' : ''}"
          >
            ${vehicle.short_name}
          </span>
        </div>`;
    })
    .join('');

  vehicleWrap.innerHTML = list;
};

const getVehicleNations = () => {
  return `
    <ul class='nation-list'>
        ${Object.keys(nationList)
          .map(nation => {
            return `
              <li 
                class='nation-item' 
                data-nation="${nation}" 
                onclick="window.dataStore.filters.nation = this.dataset.nation; window.renderVehicleList();"
              >
                <img 
                  class='nation-img' 
                  src='./images/flags/${nation}.png' 
                  alt='${nationList[nation]}'
                >
                <span>
                  ${nationList[nation]}
                </span>
              </li>`;
          })
          .join('')}
    </ul>`;
};

const getVehicleTypes = () => {
  return `
    <ul class='type-list'>
        ${Object.keys(vehicleTypes)
          .map(type => {
            return `
              <li 
                class='type-item' 
                data-type="${type}" 
                onclick="window.dataStore.filters.type = this.dataset.type; window.renderVehicleList();"
              >
                <img 
                  class='type-img' 
                  src='./images/types/${type.toLocaleLowerCase()}.png' 
                  alt='${vehicleTypes[type]}'>
                <span>
                  ${vehicleTypes[type]}
                </span>
              </li>`;
          })
          .join('')}
    </ul>`;
};

const App = () => {
  return `
    <div class="nation-wrap">
        ${getVehicleNations()}
    </div>
    <div class="type-wrap">
        ${getVehicleTypes()}
    </div>
    <div class="vehicle-wrap"></div>
    <div class="infoVehicle-wrap"></div>
  `;
};

mainApp.innerHTML = App();
